.retailers-wrapper {
  display: flex;
  min-height: 100vh;
  background-color: #f8fafc;
}

.retailers-content-area {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin-left: 250px;
  transition: margin-left 0.3s ease;
  min-height: 100vh;
}

.retailers-content-area.collapsed {
  margin-left: 80px;
}

.retailers-main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
}

/* Page Header - Replaces AdminHeader */
.retailers-page-header {
  background: #fff;
  padding: 15px 30px;
  border-bottom: 1px solid #e5e7eb;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 0;
  z-index: 100;
}

.retailers-page-header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.retailers-header-info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.retailers-page-title {
  font-size: 20px;
  font-weight: bold;
  margin: 0;
  color: #1f2937;
}

.retailers-welcome {
  font-size: 14px;
  color: #6b7280;
}

/* Content Section */
.retailers-content-section {
  flex: 1;
  padding: 30px;
  background-color: #f8fafc;
}

/* Header Top Section */
.retailers-header-top {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 24px;
  gap: 20px;
}

.retailers-title-section {
  flex: 1;
}

.retailers-main-title {
  font-size: 32px;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 8px 0;
}

.retailers-subtitle {
  font-size: 16px;
  color: #6b7280;
  margin: 0;
}

/* Add Retailer Button */
.retailers-add-button--top {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 20px;
  background-color: #2563eb;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  white-space: nowrap;
  height: fit-content;
}

.retailers-add-button--top:hover {
  background-color: #1d4ed8;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(37, 99, 235, 0.2);
}

.retailers-add-icon {
  font-size: 18px;
  font-weight: bold;
}

