.retailers-table-container table {
  min-width: 800px;
  /* Minimum width to maintain table structure */
  width: 100%;
}

/* Mobile-specific table improvements */
@media (max-width: 768px) {
  .retailers-table-container {
    border-radius: 8px;
    margin: 0 -16px;
    width: calc(100% + 32px);
  }

  .retailers-table-container table {
    min-width: 900px;
    /* Slightly larger min-width for better mobile readability */
    margin-bottom: 0;
  }

  /* Improve table cell spacing for mobile */
  .retailers-table__retailer-cell,
  .retailers-table__contact-cell,
  .retailers-table__type-location-cell,
  .retailers-table__performance-cell {
    min-width: 120px;
