  /* Adjust font sizes for better mobile readability */
  .retailers-table__retailer-name {
    font-size: 14px;
  }

  .retailers-table__retailer-id {
    font-size: 11px;
  }

  .retailers-table__contact-item,
  .retailers-table__type,
  .retailers-table__rating {
    font-size: 13px;
  }

  .retailers-table__contact-email,
  .retailers-table__location,
  .retailers-table__revenue {
    font-size: 11px;
  }

  /* Adjust action buttons for mobile */
  .retailers-table__actions {
    flex-direction: row;
    gap: 4px;
  }

  .retailers-table__action-btn {
    width: 30px;
    height: 30px;
    padding: 5px;
    font-size: 14px;
  }
